.btn {
  width: 255px;
  height: 60px;
  padding: 0;
  transition: opacity 0.1s ease-out;
  color: #fff;
  border: 0;
  background-color: #000;
  font-weight: 700;
}

.btn:hover {
  opacity: 0.7;
}

.btn:active {
  color: #fff;
  background-color: #d00000;
}

.btn:disabled {
  opacity: 0.5;
}
